import styled from 'styled-components'
import ScrollToTop from '../Components/ScrollToTop'
import About from '../Img/About.jpg'
import device from '../Components/device'
import { useState } from 'react'

const Background = styled.div`
height:100vh;
width:100%;
`
const Bild = styled.img`
background-image:url(${About});
background-size: cover;
background-position: center;
border:2px solid black;
height:80vh;
width:100%;
position:fixed;`


const Wrap = styled.div`
height:100vh;
width:100%;
background-color:white;
top:65%;

position:absolute;
z-index:5;




`
const Wrap1 = styled.div`


`


const Tytul = styled.h1`
top:0vh;
left:50%;
transform:translate(-50%);
position:absolute;
margin:50px auto;

@media(${device.mobileL}) {
   font-size:30px;
   }
`
const Text = styled.h2`
top:30vh;
left:50%;
transform:translate(-50%);
position:relative;
display:block;
overflow:hidden;
font-size:20px;
        margin:auto 20px;
       
@media(${device.mobileL}) {
    font-size:20px;
    margin:auto 20px;
    top:20vh;
    }
    @media(${device.mobileS}) {
        font-size:20px;
        margin:auto 20px;
        top:20vh;
        }
    @media(${device.tablet}) {
        font-size:20px;
        margin:auto 30px;
        top:20vh;
        }
`

const Social = styled.a`
display:block;
heigth:200px;
width:100%;
margin:20px auto;
`




const Kontakt = () => {
    const [data, setData]=useState({
        email: "kwloczewska96@gmail.com",
        handy: "0049 015215123",
        instagram: "https://www.instagram.com/catlens_",
        facebook: "https://www.facebook.com/catlensphotography"
    })
    return ( <>
    
<ScrollToTop/>
    <Background >
        <Bild/>
<Wrap>
        <Tytul>Contact Me</Tytul>

<Text >
    <p>Email: {data.email}</p>
    <p>Handy: {data.handy}</p>
    <Social>
    <a href={data.instagram}>Instagram</a>
    <br/>
    <br/>
    <a href={data.facebook}>Facebook</a>
    </Social>
<br/>
<h1 style={{textAlign:"center",fontSize:"30px"}}>And with projektant my page</h1>
<p style={{margin:"50px auto 20px"}}>Producent: Sebastian Szurma</p>
<p style={{margin:"20px auto 20px"}}>Czucziczucz</p>
<p>All rights reserved 2021</p>

</Text>

    
    
    </Wrap>
    </Background> </>
    );
}
 
export default Kontakt;