import React, {useEffect } from 'react'
import styled from 'styled-components'
import device from '../Components/device'
import { CgArrowLongDownR } from "react-icons/cg";
import {
    Switch,
    Route,
    Link,
 } from "react-router-dom";
import ScrollToTop from './ScrollToTop';


const Svg = styled.svg`
position:fixed;
top:-10%;
left:50%;
transform:translate(-50%);
height: 100vh;
opacity:0.8;

}

@media(${device.mobileL}) {
    left:50%;
 
    height:100vh;
}
@media(${device.mobileS}) {
    left:50%;
    top:-20%;
    
    
}
@media(${device.desktop}){
  


}
`
const Wrapper = styled.div`
background: linear-gradient(
    to bottom,
    GhostWhite,
    DarkSlateGray
  );
  overflow:hidden;
height:200vh;
 display:flex;
  flex-flow: column;
  align-items: center;
  align-content:center;

`

const Text = styled(Link)`
font-size:25px;
position:absolute;
top:190%;
left:10%;
transition: transform 2s linear;
color:black;
display:block;
cursor:pointer;
z-index:100;

animation: blink 2s linear infinite;


@keyframes blink {
    0% {
        text-shadow:  0 0 1px white;
    }

    50% {
        text-shadow:  0 0 2px white;
    }

    100% {
        text-shadow:  0 0 1px white;
    }
  }

& :hover  {
    text-decoration:none;
    color:white;}


    @media(${device.mobileL}) {
        top:175%;

    }
    @media(${device.mobileS}) {
            top:155%;
 }

 @media(${device.tablet}) {
   left:30%;
    top:150%; 
}
`
const ScrollDown = styled.h1`
text-align:center;
display:block;
position:absolute;
top:60%;
font-size:20px;
transition: 1s linear;
`

const SvgText = styled.svg`
top:30vh;
position:absolute;

& .cat {
   
     animation: draw 5s ease-in infinite ;
    stroke-dashoffset: 1000;
    stroke-dasharray: 1000;
}

@keyframes draw {
    from{stroke-dashoffset: 1000}
     to {stroke-dashoffset: 0}
}

@media(${device.mobileL}) {
   
}




`

const SvgText2 = styled.svg`
position:absolute;
top:145%;
display:block;
transition: 1s linear;
transform:"scale(0)";

@media(${device.mobileL}) {
    top:145%;
    left:25%;
    
}
@media(${device.mobileS}) {
    top:125%;
    left:5%;
    
}
@media(${device.tablet}) {
    left:30%;
    top:130%; 

}
& .cat1 {
   
    animation: draw 5s ease-in infinite;
   stroke-dashoffset: 1000;
   stroke-dasharray: 1000;
}

@keyframes draw {
   from{stroke-dashoffset: 1000}
    to {stroke-dashoffset: 0}
}


`




const SvgComponent = () => {
    
    const IconSvg=()=> {


      


        useEffect(()=>{

            const Welcome=()=>{ 
               
                const welcomePath = document.querySelector('.welcome')
            welcomePath.getBoundingClientRect();
            window.addEventListener("scroll",function(e){
                const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
                if(scrollProcent<=0.10){
                    welcomePath.style.transform="scale(1)"
                   
                } else { welcomePath.style.transform="scale(0)"}
            })}

            const Catlens=()=>{ 
               
                const CatPath = document.querySelector('.cat')
            CatPath.getBoundingClientRect();
            window.addEventListener("scroll",function(e){
                const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
                if(scrollProcent<=0.10){
                    CatPath.style.transform="scale(1)"
                    CatPath.style.transform=''
                } else { CatPath.style.transform="scale(0)"}
            })}

            const Catlens1=()=>{ 
               
                const CatPath = document.querySelector('.cat1')
            CatPath.getBoundingClientRect();
            window.addEventListener("scroll",function(e){
                const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
                if(scrollProcent>=0.80){
                    CatPath.style.transform="scale(1)"
                } else { CatPath.style.transform="scale(0)"}
            })}

        const TextShow=()=>{ 
            
            const textPath = document.querySelector('.text1')
        textPath.getBoundingClientRect();
        window.addEventListener("scroll",function(e){
            const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
            if(scrollProcent<=0.90){
                textPath.style.display="none"
            } else {textPath.style.display="block"}
        })}
                    
        const DrawPath=()=>{
    const path = document.querySelector('#star')
    const pathLength = path.getTotalLength()
    
    path.style.strokeDasharray=pathLength+'  '+pathLength;
    path.style.strokeDashoffset=pathLength;
    path.getBoundingClientRect();
    
    window.addEventListener("scroll",function(e){
        const scrollProcent=(document.documentElement.scrollTop+document.body.scrollTop)/(document.documentElement.scrollHeight-document.documentElement.clientHeight);
    
        const drawLenght=pathLength*scrollProcent;
        path.style.strokeDashoffset=pathLength-drawLenght;
        if (scrollProcent>=0.95) {
            path.style.strokeDasharray="none"
            
        } else {
            path.style.strokeDasharray=pathLength+' '+pathLength;
        }
    })
}
Catlens1()
Catlens()
Welcome()
DrawPath()
TextShow()

        },[])

        
        return(
<>
<Wrapper>




<SvgText>
<svg width="99.916mm" height="14.083mm" version="1.1" viewBox="0 0 46.916 13.083" xmlns="http://www.w3.org/2000/svg" >
 <g transform="translate(-101.29 -70.047)">
  <text className="cat" id="cat" transform="translate(-15.05 -.0175)" style={{fill:'none',fontFamily:'Segoe MDL2 Assets,PT Serif', fontSize:'14.933px',lineHeight:'1.25',shapeInside:'url(#rect835)',strokeWidth:'.3',stroke:'#000000',textAlign:'center',whiteSpace:'pre' }}>
      <tspan x="100.979214" y="82.549963"><tspan style={{fill:'none',fontFamily:'Segoe MDL2 Assets,PT Serif',fontSize:'16.933px',strokeWidth:'.3',stroke:'#000000'}}>Catlens</tspan></tspan></text>
 </g>
</svg>
</SvgText>

<SvgText2>
{/* <svg  width="47.236mm" height="11.249mm" version="1.1" viewBox="0 0 47.236 11.249" xmlns="http://www.w3.org/2000/svg">
 <g transform="translate(-81.164 -60.922)">
  <text className="cat1" id="cat1" fill="none" fontFamily="Forte" fontSize="14.111px" stroke="#000000" strokeWidth=".5" textAlign="center" style={{lineGeight:'1.25',shapeInside:'url(#rect2795)',whiteSpace:'pre'}} ><tspan x="81.034876" y="70.680524"><tspan fill="none" fontFamily="Forte" fontSize="14.111px" stroke="#000000" strokeWidth=".5">Catlens</tspan></tspan></text>
 </g>
</svg> */}
<svg width="99.916mm" height="13.083mm" version="1.1" viewBox="0 0 46.916 13.083" xmlns="http://www.w3.org/2000/svg" >
 <g transform="translate(-105.29 -70.047)">
  <text className="cat1" id="cat1" transform="translate(-25.05 -.79375)" style={{fill:'none',fontFamily:'Segoe MDL2 Assets,PT Serif', fontSize:'14.933px',lineHeight:'1.25',shapeInside:'url(#rect835)',strokeWidth:'.3',stroke:'#000000',textAlign:'center',whiteSpace:'pre' }}>
      <tspan x="100.979214" y="82.549963"><tspan style={{fill:'none',fontFamily:'Segoe MDL2 Assets,PT Serif',fontSize:'16.933px',strokeWidth:'.3',stroke:'#000000'}}>Catlens</tspan></tspan></text>
 </g>
</svg>
</SvgText2>

<ScrollDown className="welcome"><p>Welcome!</p><p>Scroll Down</p>
<CgArrowLongDownR /></ScrollDown>


 <Text className="text1" to="/portfolio" ><p>Photography</p></Text>

<Switch><Route path="/portfolio"></Route></Switch>

<Svg>
            <svg width="300px" height="800px" version="1.1" viewBox="0 0 118.14 289.88" xmlns="http://www.w3.org/2000/svg" >
 <defs>
  

   <path transform="matrix(.8 0 0 .8 -4.8 0)" d="m5.77 0-8.65 5v-10z" fill="#fff" fill-rule="evenodd" stroke="#000" stroke-width="1pt" />

  
 </defs>
 <g transform="translate(-43.275 8.9655)">
  <path d="m119.42-8.3155c7.5473 18.414 7.5473 39.794 0 58.208-2.4651 6.0144-5.7123 11.734-9.8715 16.729-10.018 12.031-24.567 19.129-37.601 27.802-6.5173 4.3364-12.781 9.1791-17.831 15.161s-8.8508 13.192-9.8931 20.951c-0.97662 7.2698 0.52094 14.77 3.713 21.374 3.1921 6.6041 8.0257 12.339 13.637 17.063 11.223 9.4474 25.25 14.79 39.048 19.775 13.797 4.9852 27.908 9.9129 39.628 18.737 5.8596 4.4123 11.058 9.7911 14.776 16.114s5.914 13.62 5.7308 20.952c-0.22536 9.0203-4.1296 17.851-10.475 24.267s-15.037 10.396-24.019 11.261c-8.9815 0.865-18.193-1.3459-25.937-5.976-7.7445-4.6301-14.01-11.628-17.992-19.724-1.2547-2.5509-2.29-5.2098-3.0906-7.9375" fill="none" stroke="#000" stroke-linecap="round" stroke-linejoin="round" stroke-width="1.3"  className="star" id="star"  />
 </g>
</svg>
</Svg>
</Wrapper>
</>
)}

   

        
        
    

    return(
        <>
<ScrollToTop/>
<IconSvg/>

       </>
)
}
export default SvgComponent