
import {Link, Switch, Route, BrowserRouter as Router } from 'react-router-dom'
import Home from '../Page/Home'
import About from '../Page/About'
import Kontakt from '../Page/Kontakt'
import 'bootstrap/dist/css/bootstrap.min.css'
import {useState} from 'react'
import styled from 'styled-components';
import {CSSTransition, TransitionGroup} from 'react-transition-group'
import Portrait from '../Page/Portrait'
import Animals from '../Page/Animals'
import Art from '../Page/Art'
import Architecture from '../Page/Landscape'
import Portfolio from '../Page/Portfolio'
// import ScrollToTop from './ScrollToTop'
import {device} from '../Components/device'
import { AiOutlineInstagram,AiOutlineFacebook,} from "react-icons/ai";

import {SiGmail } from "react-icons/si"
const StyleNav = styled.div`
    font-size:30px;
    height:400px;
    list-style:none;
   
    &:hover{}

    @media ${device.mobile} {}
    `
    const StyleLi = styled.li`
    margin:50px;
    transform:${props=>props.open?"translate(0%)":'translate(-150%)'};
     text-shadow: -1px -1px 1px #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
     
     &:nth-child(1){
       
      transition: .8s ease-in ;
     }
     &:nth-child(2){
      transition: .5s ease-in .5s ;
     }
     &:nth-child(3){
      transition: .3s ease-in ;
     }
     &:nth-child(4){
      transition: .3s ease-in .1s;
     }
    
    `
    const LinkWrap = styled(Link)`
    list-style: none;
    text-decoration: none;
    color:white;
    text-algin:center;
    
    
  
   
  &:hover{text-decoration: none;
        color:red;
        opacity:0.5;
    }
    @media ${device.mobile} {}
    `
    const Navigation = styled.div`
    z-Index:30;
    position:fixed;
    
    
    `
    const Instagram = styled(AiOutlineInstagram)`
    cursor:pointer;
    color:black;
    margin:auto;
    line-height: 0.5;
    `
    const Facebook = styled(AiOutlineFacebook)`
    cursor:pointer;
    margin:auto;
    color:royalblue;
    line-height: 0.5;
    `
    const Gmail = styled(SiGmail)`
    color:DarkRed;
    font-size:20px;
    `
    const WrapSocialMedia = styled.div`
top:80vh;
display:flex;
flex-flow:column;
justify-content:center;
align-items:center;
left:4%;
position:fixed;
z-index:100;
transform:translate(-50%);
font-size:25px;

height:100px;
width:60px;
border: 2px solid black;
border-right:15px solid brown;


`
const Nav=()=> {
  
  
const NavigationWrapp=()=>{
      const [open, setOpen] = useState(false)
      const [socialOpen,setSocialOpen]=useState(false)
      const [data, setData]=useState({
        email: "kwloczewska96@gmail.com",
        handy: "0049 015215123",
        instagram: "https://www.instagram.com/catlens_",
        facebook: "https://www.facebook.com/catlensphotography"
    })
      
      return(
<><Router basename={process.env.PUBLIC_URL}>
<WrapSocialMedia onClick={()=>{setSocialOpen(!socialOpen)}} style={{transform:socialOpen?"translate(-35%)":"translate(-110%)"}} ><a href={data.instagram}><Instagram/></a><a href={data.facebook}><Facebook/></a> <Gmail/></WrapSocialMedia>


    <div id="nav-icon3" 
    style={{zIndex:"30"}} 
    className={open?"open":""} 
    onClick={()=>setOpen(!open)} >
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>

    <Navigation >

    <StyleNav>
        <StyleLi
      open={open}
      >
          <LinkWrap  onClick={()=>setOpen(!open)}  to="/"><p>Home</p></LinkWrap>
        </StyleLi >
        <StyleLi open={open}>
          <LinkWrap  onClick={()=>setOpen(!open)} to="/portfolio"><p>Portfolio</p></LinkWrap>
        </StyleLi >
    <StyleLi open={open}>
          <LinkWrap  onClick={()=>setOpen(!open)} to="/about"><p>About</p></LinkWrap>
        </StyleLi >
    <StyleLi open={open}> 
          <LinkWrap onClick={()=>setOpen(!open)} to="/Kontakt"><p>Contact</p></LinkWrap>
        </StyleLi>
        </StyleNav>
        
      </Navigation>

      
  <Route render={({location})=>( <TransitionGroup>
          <CSSTransition 
          key={location.key}
          timeout={300}
          classNames="fade">
  <Switch location={location}>
        
              <Route path="/about"  component={About}>
                <About />
              </Route>
              <Route path="/Kontakt" component={Kontakt}>
                <Kontakt />
              </Route>
              <Route exact strict path="/" component={Home}>
                <Home />
              </Route>
              <Route exact strict path="/portfolio" component={Portfolio}>
                <Portfolio />
              </Route>
              <Route path="/Portrait" component={Portrait}>
                <Portrait/>
              </Route>
              <Route  path="/animals" component={Animals}>
                <Animals/>
              </Route>
              <Route path="/art" component={Art}>
                <Art/>
              </Route>
              <Route path="/architecture" component={Architecture}>
                <Architecture/>
              </Route>
           
            </Switch>
           </CSSTransition></TransitionGroup>)}>
           </Route>
          </Router>
    </>
      )
    }

    return(
  <>
    <NavigationWrapp/>
    </>
    )
    }

    export default Nav